import { createClient } from '@supabase/supabase-js'

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL || ''
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY || ''

// Client for regular app operations (uses anon key)
export const supabase = supabaseUrl && supabaseAnonKey
  ? createClient(supabaseUrl, supabaseAnonKey, {
      auth: {
        persistSession: true,
        autoRefreshToken: true,
        detectSessionInUrl: true,
        flowType: 'pkce' // Use PKCE flow for better security
      },
      global: {
        headers: {
          'X-Client-Info': 'healthscribe-dashboard'
        }
      }
    })
  : null as any

// Admin client is only available on the server side
// Use this function in server-side code (API routes) only
export const getSupabaseAdmin = () => {
  // Only run on server side
  if (typeof window !== 'undefined') {
    console.warn('⚠️ getSupabaseAdmin() should only be used on the server side')
    return null
  }

  const supabaseServiceKey = process.env.SUPABASE_SERVICE_ROLE_KEY || ''

  if (!supabaseUrl || !supabaseServiceKey) {
    console.warn('⚠️ Supabase admin client: Missing URL or service key')
    return null
  }

  return createClient(supabaseUrl, supabaseServiceKey, {
    auth: {
      persistSession: true,
      autoRefreshToken: true,
      detectSessionInUrl: true,
      flowType: 'pkce'
    },
    global: {
      headers: {
        'X-Client-Info': 'healthscribe-dashboard-admin'
      }
    }
  })
}

// Debug logging for client initialization (only in development)
if (process.env.NODE_ENV === 'development') {
  console.log('🔧 Supabase client initialization:')
  console.log('  - URL present:', !!supabaseUrl)
  console.log('  - Anon key present:', !!supabaseAnonKey)
  console.log('  - Regular client initialized:', !!supabase)
}

